/* Banner.css */
.banner {
    display: flex;
    justify-content: space-between;
    background: #FE2B54;
}
.banner .text .Customize {
    font-size: 100px;
    color: transparent;
    -webkit-text-stroke: 2px white;
    font-weight: 700;
    text-transform: uppercase;
    font-family: 'Your-Font-Name', sans-serif;
    text-align: -webkit-center;
    margin-top: 2rem;
}
.banner-text {
    font-size: 70px;
    color: #fff;
    -webkit-text-stroke: 2px white;
    text-transform: uppercase;
    font-family: none;
    text-align: left;
    margin: -4rem;
    margin-left: 4%;
}
.text {
   flex: 1;
   padding: 20px;
}
.describe {
    padding: 100px 0px 40px 31px;
    color:#fff;
}
.image {
    flex: 1;
    text-align: center; /* If you want to center the image horizontally */
    align-self: center; /* If you want to center the image vertically */
  }
  
.discover-button {
    background-color: #000;
    color: #fff;
    font-size: 24px;
    padding: 10px 20px;
    border: none;
    /* margin: 0%; */
    margin: 0px 0px 30px 30px;
    cursor: pointer;
}
.homepage {
    padding: 20px;
  }
  
  .top-bar {
    display: flex;
    justify-content: space-between;
  }
  
  .product-grid {
    display: flex;
    margin: 1rem;
}
  
.product-card {
  width: 25%;
  padding: 12px;
  text-align: center;
}
  
  .product-card img {
    max-width: 100%;
    border: 1px solid #ccc;
  }
  .product-image-container img {
    border: none;
}
.product-image-container{
  background: #EFEFEF;
}
  .product-details {
    margin-top: 10px;
    text-align: left;
  }
  
  .favorite {
    font-size: 18px;
    font-weight: bold;
    color: #f00;
  }
  
  .product-description {
    font-size: 16px;
  }
  
  .color-choice {
    margin-top: 10px;
  }
  
  .color-circle {
    display: inline-block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    margin: 0 5px;
  }
  
  .product-price {
    margin-top: 10px;
    font-weight: bold;
  }
  
  .buttons {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
  }
  
  .customize-button,
  .add-to-cart-button {
    width: 45%;
    padding: 10px;
    background-color: #0074d9;
    color: #fff;
    border: none;
    cursor: pointer;
  }
  .product-card .star-rating {
    text-align: right;
}
  .add-to-cart-button {
    background-color: #2ecc40;
  }
  
  .customize-button:hover,
  .add-to-cart-button:hover {
    background-color: #0056b3;
  }
  