.navbar{
    background: black;
    padding: 1% 0% 1% 2%;
    color: #ffffff;
}
.navbar-logo
{
    display: flex;
    align-items: center;    
}
.navbar-logo span {
    /* margin: 0.5rem; */
    padding-left: 1%;
}
.navbar-links {
    margin: -0.5rem;
    display: flex;
}
.Navbaritems {
    margin: 0.75rem;
    text-decoration: none;
    border-left: 2px solid #007bff;
    padding-left: 10px;
    color: #fff;
}
.Navbaritems:hover {
    background-color: #f0f0f0;
    /* Add padding and adjust border color for hover effect */
    padding-left: 12px; /* Increase padding on hover */
    border-left-color: #ff9900; /* Change border color on hover */
}
.navbar-middle{
    background-color: #25F4EE;
    text-align: center;
    display: grid;
}
.navbar-middle h1 {
    margin: 0rem;
}