.shop-now-section {
  display: flex;
  background: #000;
}
  .shop-now-content {
    flex: 1; /* Takes up the available space on the left */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
  }
  
  .shop-now-content img {
    max-width: 100%; /* Ensure the image fits within its container */
  }
  
  .shop-now-button {
    background-color: #007bff;
    color: white;
    padding: 10px 20px;
    border: none;
    cursor: pointer;
    font-size: 16px;
  }
  .btn-shop-now
    {
        display: flex;
    }  
  .svg-image {
    flex: 1; /* Takes up the available space on the right */
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
  }
  